import { Token } from '@lumino/coreutils';
import { ILayoutRestorer } from '@jupyterlab/application';
import { MainAreaWidget, ICommandPalette, WidgetTracker } from '@jupyterlab/apputils';
import { ISettingRegistry } from '@jupyterlab/settingregistry';
import { ILauncher } from '@jupyterlab/launcher';
import icon from '@datalayer/icons-react/data1/LaptopSimpleIconLabIcon';
import { requestAPI } from './handler';
import { JupyterSSHWidget } from './widget';
import { Timer } from "../state";
import { TimerView } from "../timer/TimerView";
import { mobxTimer, MobxTimerView } from "../state/mobx";
import '../../style/index.css';
export const IJupyterSSH = new Token('@datalayer/jupyter-ssh:plugin');
/**
 * The command IDs used by the plugin.
 */
var CommandIDs;
(function (CommandIDs) {
    CommandIDs.create = 'create-jupyter-ssh-widget';
})(CommandIDs || (CommandIDs = {}));
/**
 * Initialization data for the @datalayer/jupyter-ssh extension.
 */
const plugin = {
    id: '@datalayer/jupyter-ssh:plugin',
    autoStart: true,
    requires: [ICommandPalette],
    optional: [ISettingRegistry, ILauncher, ILayoutRestorer],
    provides: IJupyterSSH,
    activate: (app, palette, settingRegistry, launcher, restorer) => {
        const { commands } = app;
        const command = CommandIDs.create;
        const tracker = new WidgetTracker({
            namespace: 'jupyter-ssh',
        });
        if (restorer) {
            void restorer.restore(tracker, {
                command,
                name: () => 'jupyter-ssh',
            });
        }
        const jupyterSSH = {
            timer: new Timer(),
            TimerView,
            mobxTimer,
            MobxTimerView,
        };
        commands.addCommand(command, {
            caption: 'Show SSH',
            label: 'SSH',
            icon,
            execute: () => {
                const content = new JupyterSSHWidget(app, jupyterSSH);
                const widget = new MainAreaWidget({ content });
                widget.title.label = 'SSH';
                widget.title.icon = icon;
                app.shell.add(widget, 'main');
                tracker.add(widget);
            }
        });
        const category = 'Datalayer';
        palette.addItem({ command, category });
        if (launcher) {
            launcher.add({
                command,
                category,
                rank: 2.3,
            });
        }
        if (settingRegistry) {
            settingRegistry
                .load(plugin.id)
                .then(settings => {
                console.log('@datalayer/jupyter-ssh settings loaded:', settings.composite);
            })
                .catch(reason => {
                console.error('Failed to load settings for @datalayer/jupyter-ssh.', reason);
            });
        }
        requestAPI('config')
            .then(data => {
            console.log(data);
        })
            .catch(reason => {
            console.error(`Error while accessing the jupyter server jupyter_ssh extension.\n${reason}`);
        });
        console.log('JupyterLab plugin @datalayer/jupyter-ssh is activated!');
        return jupyterSSH;
    }
};
export default plugin;
